.header__container {
  display: flex;
  align-items: center;
  min-height: 114px;
}
.header__menu {
  flex: 1 1 auto;
}
.header__button {
  flex: 0 0 252px;
  display: flex;
  justify-content: flex-end;
}

.main {
  flex: 1 1 auto;
}

.menu {
  display: flex;
  justify-content: flex-end;
}
.menu__list {
  display: flex;
}

.button {
  border-radius: 5px;
  padding: 15px 30px;
  background-color: #ec5863;
  transition: background-color 0.3s ease 0s;
}
.button__text {
  color: #fcfdfe;
  font-family: Raleway, sans-serif;
  font-weight: 800;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 10px;
}
.button_blue {
  background-color: #4285f4;
}
.button:hover {
  background-color: #70a3f7;
}